import React from 'react';
import { Row, Col, Form } from 'react-bootstrap';

export default function RadioBoxType3(props) {
    const numbers = [1, 2, 3];
    return (
        <div>
            <span className="dis_b mt10 mb10">
                {props.question}
                {props.etc === 'true' && (
                    <Form.Control
                        type="text"
                        name="schoolname"
                        placeholder="기타 내용 입력"
                        className="he40 we100 mt10"
                    />
                )}
            </span>
            <Row>
                <Col className="fs_10">그렇다</Col>
                <Col className="fs_10 text-center">보통이다</Col>
                <Col className="fs_10 text-right">아니다</Col>
            </Row>
            <Form.Group className="mt10">
                <Row>
                    {numbers.map((value, i) => (
                        <Col
                            className={
                                value > 2
                                    ? 'fs_10  text-right'
                                    : value === 2
                                    ? 'fs_10  text-center'
                                    : 'fs_10'
                            }
                            key={i}
                        >
                            <label className="radio-inline">
                                <Form.Check.Input
                                    type="radio"
                                    id={props.id}
                                    name={props.name}
                                    value={value}
                                    className="mb5"
                                />
                                <span className="indicator"></span>
                                {value}
                            </label>
                        </Col>
                    ))}
                </Row>
            </Form.Group>
        </div>
    );
}
