import React from 'react';
import { Row, Col, Form } from 'react-bootstrap';

export default function RadioBoxType1(props) {
    const numbers = [1, 2, 3, 4, 5];
    return (
        <div>
            <Row>
                <Col className="">전혀 그렇지 않다</Col>
                <Col className=" text-center">보통</Col>
                <Col className=" text-right">매우 그렇다</Col>
            </Row>
            <Form.Group className="mt10">
                <Row>
                    {numbers.map((value, i) => (
                        <Col
                            className={
                                value > 3
                                    ? 'fs_10  text-right'
                                    : value === 3
                                    ? 'fs_10  text-center'
                                    : 'fs_10'
                            }
                            key={i}
                        >
                            <label className="radio-inline">
                                <Form.Check.Input
                                    type="radio"
                                    id={props.id}
                                    name={props.name}
                                    value={value}
                                    className="mb5"
                                />
                                <span className="indicator"></span>
                                {value}
                            </label>
                        </Col>
                    ))}
                </Row>
            </Form.Group>
        </div>
    );
}
