import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import SelectBox from '../template/SelectBox';

export default function Form11(props) {
    const [state, setState] = useState(false);
    return (
        <div>
            <Form.Group className="mt10">
                <label className="radio-inline">
                    <Form.Check.Input
                        type="radio"
                        id={props.id}
                        name={props.name}
                        value="1"
                        className="mb5"
                        onChange={(e) => setState(false)}
                    />
                    <span className="indicator"></span>예
                </label>
                <label className="radio-inline ml30">
                    <Form.Check.Input
                        type="radio"
                        id={props.id}
                        name={props.name}
                        value="2"
                        className="mb5"
                        onChange={(e) => setState(true)}
                    />
                    <span className="indicator"></span>
                    아니오
                </label>
            </Form.Group>
            {state === true && (
                <>
                    <label className="mt40 pb20 fs_18">
                        11-1. 충북지역의 중소기업에 취업하지 않는 이유는
                        무엇입니까?
                    </label>

                    <Form.Group className="mt10">
                        <label className="radio-inline">
                            <SelectBox
                                id={props.id + '-1'}
                                name={props.name + '-1'}
                                labeltext="1순위"
                                etc="true"
                                text="낮은 인지도, 낮은 연봉, 낮은 접근성, 낮은 성장가능성, 낮은 복리후생, 기타"
                            />
                        </label>
                        <label className="radio-inline ml30">
                            <SelectBox
                                id={props.id + '-2'}
                                name={props.name + '-2'}
                                labeltext="2순위"
                                etc="true"
                                text="낮은 인지도, 낮은 연봉, 낮은 접근성, 낮은 성장가능성, 낮은 복리후생, 기타"
                            />
                        </label>
                    </Form.Group>
                </>
            )}
        </div>
    );
}
