import React from 'react';
import { Row, Col, Form } from 'react-bootstrap';

export default function RadioBoxType2(props) {
    const numbers = [1, 2, 3, 4, 5];
    return (
        <div>
            <span
                className={
                    props.bigtitle === 'true'
                        ? 'mt40 pb20 fs_18'
                        : 'dis_b mt10 mb10'
                }
            >
                {props.question !== '' && props.question}
                {props.etc === 'true' && (
                    <Form.Control
                        type="text"
                        name={props.name + '_etc'}
                        placeholder="기타 내용 입력"
                        className="he40 we100 mt10"
                    />
                )}
            </span>
            {props.type === '1' && (
                <Row>
                    <Col className="fs_10">전혀 유용하지 않다.</Col>
                    <Col className="fs_10 text-center">보통</Col>
                    <Col className="fs_10 text-right">매우 유용하다</Col>
                </Row>
            )}

            {props.type === '2' && (
                <Row>
                    <Col className="fs_10">전혀 중요하지 않다.</Col>
                    <Col className="fs_10 text-center">보통</Col>
                    <Col className="fs_10 text-right">매우 중요하다.</Col>
                </Row>
            )}

            <Form.Group className="mt10">
                <Row>
                    {numbers.map((value, i) => (
                        <Col
                            className={
                                value > 3
                                    ? 'fs_10  text-right'
                                    : value === 3
                                    ? 'fs_10  text-center'
                                    : 'fs_10'
                            }
                            key={i}
                        >
                            <label className="radio-inline">
                                <Form.Check.Input
                                    type="radio"
                                    id={props.id}
                                    name={props.name}
                                    value={value}
                                    className="mb5"
                                />
                                <span className="indicator"></span>
                                {value}
                            </label>
                        </Col>
                    ))}
                </Row>
            </Form.Group>
        </div>
    );
}
