import React from 'react';

export default function TextSort(props) {
    const textArray = props.text.split(',');
    return (
        <div>
            <ul>
                {textArray.map((value, i) => (
                    <li key={i} className="pt5">
                        ({i + 1}) {value}
                    </li>
                ))}
            </ul>
        </div>
    );
}
