import React, { useState } from 'react';
import { Form } from 'react-bootstrap';

export default function FormType1(props) {
    const [state, setState] = useState(false);
    const textArray = props.text.split(',');
    const lastArray = textArray[textArray.length - 1];
    return (
        <div>
            <Form.Group className="mt10">
                {textArray.map((value, i) => (
                    <div className="custom-control custom-radio pz" key={i}>
                        <label className="radio-inline">
                            <Form.Check.Input
                                type="radio"
                                id={props.id}
                                name={props.name}
                                value={i + 1}
                                className="mb5"
                                onChange={(e) => {
                                    textArray[e.target.value] ===
                                    textArray[lastArray]
                                        ? setState(true)
                                        : setState(false);
                                }}
                            />
                            <span className="indicator"></span>
                            {value}
                        </label>
                    </div>
                ))}
                {state === true && (
                    <Form.Control
                        type="text"
                        name={props.name + '_etc'}
                        placeholder="기타 내용 입력"
                        className="he40 we100"
                    />
                )}
            </Form.Group>
        </div>
    );
}
