import React, { useState } from 'react';
import { Form } from 'react-bootstrap';

export default function SelectBox(props) {
    const [state, setState] = useState(false);
    const textArray = props.text.split(',');
    return (
        <div>
            <label className="radio-inline mb20">
                <Form.Label>{props.labeltext}</Form.Label>
                <Form.Control
                    as="select"
                    id={props.name}
                    name={props.name}
                    onChange={(e) => {
                        textArray[e.target.value - 1] ===
                        textArray[textArray.length - 1]
                            ? setState(true)
                            : setState(false);
                    }}
                >
                    <option>선택</option>
                    {textArray.map((value, i) => (
                        <option key={i} value={i + 1}>
                            {value}
                        </option>
                    ))}
                </Form.Control>
            </label>
            {props.etc === 'true' && state === true && (
                <Form.Control
                    type="text"
                    name={props.name + '_etc'}
                    placeholder="기타 내용 입력"
                    className="he40 we100"
                />
            )}
        </div>
    );
}
