import React from 'react';
import { Form } from 'react-bootstrap';

export default function CheakBox(props) {
    return (
        <div>
            <label className="checkbox-inline ">
                <Form.Check.Input
                    type="checkbox"
                    id="{props.id}"
                    name="{props.name}"
                    value="1"
                    className="mb5"
                />
                <span className="indicator"></span>
                {props.text}
            </label>

            {props.etc === 'true' && (
                <Form.Control
                    type="text"
                    name="schoolname"
                    placeholder="기타 내용 입력"
                    className="he40 we100 mt10"
                />
            )}
        </div>
    );
}
